@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

.topContent{
    color: white;
    position: relative;
}

.topContent__container{
    position: absolute;
    top: 300px;
    right: 150px;
}
.topContent__container>p{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 3px;
    padding-top: 20px;
}
.topContent__container>a>button{
    margin-top: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;
}

.topContent__container>h1{
    font-size: 50px;
    font-family: "Montserrat",sans-serif;
}

.topContent__downloadbutton{
    color: Black;
    background-color: yellow;
    font-size: 14px;
    padding: 10px 30px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;
}
.topContent__workbutton{
    color: white;
    border: 1px solid white !important;
    background-color: transparent;
    font-size: 14px;
    padding: 9px 45px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;
}

.topContent__downloadbutton:hover{
    color: yellow;
    background-color: black;
    font-weight: 600;
}

.topContent__workbutton:hover{
    color: black;
    background-color: white;
    font-weight: 600;
}

